@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400;500&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: 'Ubuntu', sans-serif; 
  font-size: 20px;
}


div.app {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    width: 90%;
    margin: 0 auto;
    color: #222;
}


header {
    flex-basis: 30vh;
}

main {
    /* flex grow aby main rósł, ma zajmować całą dostępną przestrzeń */
    flex-grow: 1;
    display: flex;
    padding: 30px 0;
}

aside {
    min-width: 240px;
}

section.page {
    flex-grow: 1; 
    /* rośnie na pozostałe dostępne miejsce na stronie */
}

footer {
    background-color: #000;
    color: white;
    padding: 10px 0;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 30px;
}
header {
    position: relative;
    overflow: hidden;
}

header img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 100%;
    min-height: 100%;
}
nav.main {
    padding: 0 30px 30px 0;
}

.main ul {
    list-style: none;
}

nav.main a {
    display: block;
    text-decoration: none;
    text-transform: uppercase;
    color: black;
    font-weight: 500;
    padding: 10px;
    transition: .3s;
}

nav.main a:hover {
    transform: translateX(20px);
}

nav.main a.active {
    border-left: 10px solid gray;
    transform: translateX(0);
}

nav.main li:nth-child(1) a.active {
    border-left: 10px solid green;
}

nav.main li:nth-child(2) a.active {
    border-left: 10px solid royalblue;
}

nav.main li:nth-child(3) a.active {
    border-left: 10px solid orange;
}

nav.main li:nth-child(4) a.active {
    border-left: 10px solid red;
}


.products h2 {
    margin-bottom: 20px;
}

.products ul {
    list-style: square;
    padding-left: 30px;
}

.products li {
    margin-bottom: 20px;
}

.products a {
    text-decoration: none;
    color: #333;
}

.products a:hover {
    text-decoration: underline;
    color: black;
}
.contact textarea {
    font-size: 16px;
    width: 80%;
    min-height: 200px;
    margin: 20px 0;
    line-height: 1.6
}

.contact button {
    display: block;
    margin: 0 20% 0 auto;
    border: 2px solid black;
    outline: none;
    background-color: #fff;
    padding: 10px 20px;
    cursor: pointer;
    transition: .3s;
    text-transform: uppercase;
    font-weight: 300;
}

.contact button:hover {
    background-color: black;
    color: white;
}

div.login {
    display: flex;
    flex-direction: column;
}

.login input {
    margin-top: 10px;
    padding: 5px;
    border-radius: 5px;
}

button.loginBtn {
    width: 100px;
    padding: 10px 20px;
    border: 1px solid black;
    background-color: #fff;
    cursor: pointer;
    margin-top: 20px;
    background-color: rgb(238, 238, 238);    
    text-transform: uppercase;
    transition: .3s;
}

button.loginBtn:hover {
    background-color: #03ADEB;
    color: white;
}
footer span {
    color: red;
}

footer p {
    font-size: 14px;
    margin-top: 10px;
}
